.App {
  text-align: center;
}

body{
  color: white;
}

.traits-container img{
  display: block;
  width: 100%;
  border: 3px solid #ffff;
  border-radius: 20px;
}

.traits-container{
  display: flex;
  justify-content: center;
  align-items: center;
}

.row-traits{
  display: flex;
  gap: 50px;
}

.left-traits,
.right-traits{
  display: flex;
  flex-direction: column;
  justify-content: center;
  
}

.display-image{
  display: flex;
  position: relative;
}

.function-traits{
  text-align: center;
}

.global-rarity{
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.query-traits span{
  padding: 10px 10px;
  border-radius: 10px;
  cursor: pointer;
  background-color: #069c06;
  border: 2px solid yellow;
  transition: .3s ease all;
}

.query-traits{
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.query-traits span:hover{
  border: 2px solid #0000;
  background-color: #000000;
}

.query-up,
.query-down{
  display: flex;
  justify-content: center;
  align-items: center;
}

.query-up{
  gap: 10px;
}

.trait-magiceden{
  width: 100%;
}

.query-down{
  height: 40px;
  gap: 5px;
}

.query-down input{
  width: 100%;
  height: 40px;
  outline: none;
  padding: 0 1em;
  border-radius: 10px;
}

.detail-traits {
  text-align: center;
}

.description-box{
  background-color: #ffff;
  padding: 1em;
  gap: 20px;
  border-radius: 10px;
  color: black;
  display: flex;
}

.description-left,
.description-right{
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.description-box h4,
.description-box p{
  padding: 0;
  margin: 0;
}

.trait-rarity{
  display: flex;
  flex-direction: column;
}

.tr-up,
.tr-down{
  display: flex;
  padding: .5em 0;
  min-width: 250px;
  justify-content: space-between;
  align-content: center;
}

.tr-down{
  padding: .5em .5em;
  color: #ffff;
  background-color: rgb(131, 75, 223);
}

.uil{
  padding: 0;
  margin: 0;
  cursor: pointer;
}

.uil-angle-left,
.uil-angle-right{
  position: absolute;
  font-size: 36px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #069c06;
  border-radius: 20px;
  border: 2px solid #ffff;
  transition: .3s ease;
}

.uil-angle-left:hover,
.uil-angle-right:hover{
  background-color: #000;
}

.uil-angle-left{
  top: 50%;
  left: 0%;
  transform: translate(-50%, -50%);
}
.uil-angle-right{
  top: 50%;
  left: 100%;
  transform: translate(-50%, -50%);
}